.tile-root.black{
    background-image: url('../../../assets/imgs/tiles/tile-dark.jpg');
}

.tile-root.white{
    background-image: url('../../../assets/imgs/tiles/tile-white.jpg');
}

.tile-root{
    cursor: pointer;
}

.white:hover, .black:hover{
    transition: all 0.15s ease-in;
    -webkit-box-shadow: 0px 0px 8px 4px #3a45598f inset;
    -moz-box-shadow: 0px 0px 8px 4px #3a45598f inset;
    box-shadow: 0px 0px 8px 4px #3a45598f inset;
}
