.board-root{
  height: 0;
  overflow: hidden;
  width: 100%;
  padding-top: 100%;
  position: relative;
}

.resizable-square-board{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; 
}

.horizontal-plate{
  min-height: 25px;
}
.vertical-plate{
  min-width: 25px;
}
.board-horizontal-identifier{
  width: 12.5%;
}

.board-vertical-identifier{
  height: 12.5%;
}

.angle-square-space{
  min-width: 25px;
  min-height: 25px;
}